
/* -------  project  ------- */
.project {
  margin:2em 0 4em;

  /* -------  project date  ------- */
  &__date {
    color:$color__grey;
  }

  /* -------  project preview  ------- */
  &__preview {
    display:flex;
    align-items:center;
    margin:1em 0;
  }

  /* -------  project intro  ------- */
  &__intro {
    text-align:center;
    margin-bottom:0.5em;
  }

  /* -------  project icon to expand details  ------- */
  &__icon--expand {
    width:33.3%;
    float:right;
    font-weight:500;
    height:1.5em;
    &::after {
      content:'+';
      transition:transform $timing__medium;
    }
    &.expanded::after {
      transform:rotate(45deg);
    }
  }

  /* -------  project details  ------- */
  &__details {
    clear:both;
    overflow-y:hidden;
    max-height:0;
    counter-reset:section;
    transition:max-height $timing__medium;
  }

  /* -------  media queries  ------- */
  @media only screen and (min-width:$medium) {
    width:48%;
  }
}


.project:not(:last-child)::after { content:'';display:block;background:$color__black;width:66.6%;height:2px;margin:3em auto }
.project:last-child { margin-bottom:6em }
.preview { display:flex;align-items:center }
.preview .x { display:none;width:52px;height:52px;position:absolute;top:0;right:0;cursor:pointer }
// .preview figure { text-align:center;margin:1em 0 }
.preview img { cursor:pointer;display:block;margin:0 auto }
.preview figcaption { color:$color__grey;font-size:0.9em; }
.hippify img { border:1px solid $color__black;padding:2% 4% }
.klopro img { border:1px solid $color__black;background:$color__white;padding:2% 4% 0 }
.fullscreen img { border:0 }


