.expanded::after { transform:rotate(180deg) }


.fullscreen { z-index:999;position:absolute;top:0;bottom:0;left:0;right:0;background:rgba(0,0,0,0.9) }
.fullscreen .x { display:block }
.fullscreen figure { padding:10% 0 }
.fullscreen img { width:94%;border-radius:2px;cursor:initial }
.fullscreen figcaption { color:$color__white }
.x .line  { height:2px;width:24px;display:block;background:$color__white;position:absolute;top:25px;left:14px }
.x .line-top { transform:rotate(-45deg) }
.x .line-bottom { transform:rotate(45deg) }
